h1 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 32px;
}

h2 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 24px;
}

h3 {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
}

p {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
}

section {
  padding: 4px;
  display: flex;
  gap: 4px;
  width: auto;
  align-items: center;
  box-sizing: border-box;
}
section:hover {
  color: var(--text-base-text-default);
  background: var(--background-bg-2);
  border-radius: 4px;
}
section .label-container {
  display: inline;
  align-items: center;
  box-sizing: border-box;
  text-align: left;
  width: 100%;
}

.label--disabled--light-theme {
  pointer-events: none;
}
.label--disabled--light-theme .label-container {
  color: var(--state-disabled);
}

.label--disabled--dark-theme {
  pointer-events: none;
}
.label--disabled--dark-theme .label-container {
  color: var(--state-disabled--dark);
}

.label--size--base {
  height: 24px;
}
.label--size--base .label-container {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
}
.label--size--base .symbol-padding--left {
  padding-left: 16px;
}
.label--size--base .symbol-padding--right {
  padding-right: 16px;
}

.label--size--mid {
  height: 32px;
}
.label--size--mid .label-container {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}
.label--size--mid .symbol-padding--left {
  padding-left: 24px;
}
.label--size--mid .symbol-padding--right {
  padding-right: 24px;
}

.label--size--large {
  height: 40px;
}
.label--size--large .label-container {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
}
.label--size--large .symbol-padding--left {
  padding-left: 32px;
}
.label--size--large .symbol-padding--right {
  padding-right: 32px;
}

.label--size--extra-large {
  height: 48px;
}
.label--size--extra-large .label-container {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: 48px;
}
.label--size--extra-large .symbol-padding--left {
  padding-left: 48px;
}
.label--size--extra-large .symbol-padding--right {
  padding-right: 48px;
}

.label--theme--light-theme {
  color: var(--text-base-text-default);
}
.label--theme--light-theme:hover {
  color: var(--text-base-text-default);
  background: var(--background-bg-2);
  border-radius: 4px;
}

.label--theme--dark-theme {
  color: var(--text-base-text-default--dark);
}
.label--theme--dark-theme:hover {
  color: var(--text-tertiary-text-default);
  background: var(--background-bg-2--dark);
  border-radius: 4px;
}

.label-with-owner .label-container {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
